
.music-play-page {
	width: 100%;
	max-width: 68vh;
	height: 100%;
	position: fixed;
	top: 0;
	z-index: 5;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	background: #ccdddd;
}
.playImg {
	font-size: 0;
}
.playImg img {
	width: 100%;
}
.progress-box {
	width: 100%;
	height: 5px;
	background: #ccc;
}
.progress {
	height: 100%;
	width: 0;
	background: #FF2D55;
}
.huakuai {
	float: right;
	width: 2px;
	height: 10px;
	background: #FF2D55;
}
.time-box {
	box-sizing: border-box;
	width: 100%;
	padding: 0 8px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
}
.play-bottom{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
}
.music-lrc{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	text-align: center;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
}
.song-title{
	color: #111;
}
.song-singer{
	display: block;
	color: #A9A6A6;
	font-size: 12px;
	padding: 5px 0;
}
.music-lrc .lrc-bottom{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
}
.control{
	width: 100%;
	height: 70px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}
.control div{
	width: 15%;
	text-align: center;
}
.control div img{
	width: 60%;
}
#backhome{
	position: absolute;
	width: 30px;
	height: 30px;
	padding: 5px;
	text-align: center;
	line-height: 30px;
	color: #fff;
	background: rgba(0,0,0,.3);
	top: 10px;
	left: 10px;
	border-radius: 50%;
}
#backhome:active{
	background: rgba(0,0,0,.9);
}
#backhome img{
	width: 100%;
	height: 100%;
}



