
* {
	margin: 0;
	padding: 0;
}
html {
	overflow-x: hidden;
	background: #EEEEEE;
}
body {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	overflow-x: hidden;
}
#app {
	font-family: -apple-system, BlinkMacSystemFont, "PingFang SC", "Helvetica Neue", STHeiti, "Microsoft Yahei", Tahoma, Simsun, sans-serif;
	width: 100%;
	height: 100%;
}
#app a {
	color: #42b983;
	text-decoration: none;
}
#play-bar {
	position: fixed;
	bottom: 0;
	width: 100%;
	height: 50px;
	background: -webkit-linear-gradient(top, #f9f9f9, #f3f3f3);
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	z-index: 2;
}
.play-bar-image-container {
	width: 37.5px;
	height: 37.5px;
	padding-left: 15px;
	cursor: pointer;
}
.play-bar-image {
	width: 37.5px;
	height: 37.5px;
	border-radius: 5px;
	display: inline-block;
}
.play-bar-text {
	-webkit-box-flex: 1;
	    -ms-flex-positive: 1;
	        flex-grow: 1;
	padding-left: 10px;
	cursor: pointer;
}
.play-bar-button {
	width: 20px;
	height: 20px;
	padding-right: 15px;
	cursor: pointer;
}
.page-slide-enter-active {
	-webkit-transition: all .3s ease;
	transition: all .3s ease;
}
.page-slide-leave-active {
	-webkit-transition: all .3s ease-out;
	transition: all .3s ease-out;
}
.page-slide-enter,
.page-slide-leave-active {
	-webkit-transform: translateX(100%);
	        transform: translateX(100%);
}
.fade-enter-active {
	-webkit-transition: all .3s ease;
	transition: all .3s ease;
}
.fade-leave-active {
	-webkit-transition: all .3s ease-out;
	transition: all .3s ease-out;
}
.fade-enter,
.fade-leave-active {
	opacity: 0;
}
.play-slide-enter-active {
	-webkit-transition: all .3s ease;
	transition: all .3s ease;
}
.play-slide-leave-active {
	-webkit-transition: all .3s ease-out;
	transition: all .3s ease-out;
}
.play-slide-enter,
.play-slide-leave-active {
	-webkit-transform: translateY(100vh);
	        transform: translateY(100vh);
}
.bar-slide-enter-active {
	-webkit-transition: all .3s ease;
	transition: all .3s ease;
}
.bar-slide-leave-active {
	-webkit-transition: all .3s ease-out;
	transition: all .3s ease-out;
}
.bar-slide-enter,
.bar-slide-leave-active {
	margin-bottom: -50px;
}
@media screen and (min-width: 68vh) {
body {
		width: 68vh;
		margin: 0 auto;
}
#play-bar {
		width: 68vh;
}
}
/*border-1px 部分*/
.border-1px {
	position: relative;
}
.border-1px-after:after {
	border-top: 1px solid #d0d0d0;
	content: ' ';
	display: block;
	width: 100%;
	position: absolute;
	left: 0;
}
.border-1px-before:before {
	border-top: 1px solid #d0d0d0;
	content: ' ';
	display: block;
	width: 100%;
	position: absolute;
	left: 0;
}
.border-1px:before {
	top: 0;
}
.border-1px:after {
	bottom: 0;
}
@media (-webkit-min-device-pixel-ratio: 1.5),
(min-device-pixel-ratio: 1.5) {
.border-1px:after,
	.border-1px:before {
		-webkit-transform: scaleY(.7);
		-webkit-transform-origin: 0 0;
		transform: scaleY(.7);
}
.border-1px:after {
		-webkit-transform-origin: left bottom;
}
}
@media (-webkit-min-device-pixel-ratio: 2),
(min-device-pixel-ratio: 2) {
.border-1px:after,
	.border-1px:before {
		-webkit-transform: scaleY(.5);
		transform: scaleY(.5);
}
}
@-webkit-keyframes imgFadeIn {
from {
		opacity: 0;
}
to {
		opacity: 1;
}
}
@keyframes imgFadeIn {
from {
		opacity: 0;
}
to {
		opacity: 1;
}
}
img[lazy=loaded] {
	-webkit-animation-duration: 1s;
	animation-duration: 1s;
	-webkit-animation-fill-mode: both;
	animation-fill-mode: both;
	-webkit-animation-name: imgFadeIn;
	animation-name: imgFadeIn;
}
img[lazy=error] {
	border-radius: 2px;
	-webkit-animation-duration: 1s;
	animation-duration: 1s;
	-webkit-animation-fill-mode: both;
	animation-fill-mode: both;
	-webkit-animation-name: imgFadeIn;
	animation-name: imgFadeIn;
}
.swiper-box {
	width: 100%;
	height: 100%;
	margin: 0 auto;
}
.swiper-item {
	height: 100%;
	padding: 55px 0;
}
.swiper-pagination-bullet-custom {
	width: 100% !important;
	height: 100% !important;
	margin: 0 !important;
	text-align: center;
	line-height: 50px;
	color: #999999;
	border-radius: 0 !important;
	background: #fff !important;
	opacity: 1 !important;
}
.swiper-pagination-bullet-custom.swiper-pagination-bullet-active {
	color: #000;
}
.swiper-pagination {
	top: 0;
	height: 50px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	width: 100%;
}
.routerBtn{
	width: 100%;
	height: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
}



